import '../public/config'
global.public = {
    server:getData,
    Inquire:Inquire,
    is_objEmpty:is_objEmpty,
    is_ObjKeyEmpty:is_ObjKeyEmpty,
    is_arrEmpty:is_arrEmpty,
    getKey:getKey,
    login_code:login_code,
    err_msg:null,
    second:0,
    path:null,
    msg:error,
    sort:sort
};

function sort(data){
    let temp = [];
    for(var i=data.length-1;i>=0;i--){
        temp.push(data[i]);
    }
    return temp;
}

function getData(server_name,methods,data_str,succ){

    const _url = global._config.GetData_url; 

    if(methods==='GET'||methods==='DELETE'){
        fetch(_url+"/"+server_name+"/"+data_str,{
            method: methods,
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "text/plain"
            },
        }).then(res => res.json()).then(
            data => {
                return succ(data);
            }
        ).catch(function(err){
              throw err;
        })
    }else if(methods==='POST'||methods==='PUT'){
        fetch(_url+"/"+server_name,{
            method: methods,
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "text/plain"
            },
            body:data_str
        }).then(res => res.json()).then(
            data => {
                return succ(data);
            }
        ).catch(function(err){
            throw err;
        })
    }

}

function login_code(my){
    let stu = JSON.parse(localStorage.getItem('drip_login'));
    try{
        if(my){
            if(!stu){
                let data = {
                    user:localStorage.getItem('drip_user'),
                    password:localStorage.getItem('drip_code')
                }
                getData('login','POST',JSON.stringify(data),function(data){
                    if(is_ObjKeyEmpty('code',data)){
                        if(!data.code){
                            global.public.msg('/index','非法路径跳转',6,my);
                        }
                    }
                });
            }        
        }
    }catch(error){
        console.log(error);
    }

}

function error(path='',msg='',second=0,my){
    if(my){
        global.public.path = path;
        global.public.err_msg = msg;
        global.public.second = second;
        my.props.history.push('/error');
    }
}

/*
*@method Inquire
*@param {Type} data 判断数据
*@returns {String} 数据类型 
*@desc 判断数据类型
*@throw 不在判断范围内数据类型
*/
        
function Inquire(data){
    var   gettype = Object.prototype.toString;
    switch(gettype.call(data)){
        case '[object Array]':
            return 'array';
        case '[object Object]':
            return 'object'; 
        case '[object String]': 
            return 'string'; 
        case '[object Boolean]':
            return 'boolean'  
        case '[object Undefined]':
            return 'undefined';
        case '[object Null]':
            return 'null';  
        case '[object Function]':
            return 'function';      
        default:
            console.log('无法识别类型'); 
            return false;  
    }
}

/*
*@method is_objEmpty
*@param {Object} obj 类型为对象的数据
*@returns {Boolean} trur or false
*@desc 判断对象是否为空
*/  
function is_objEmpty(obj={}){
    if(Inquire(obj)==='object'){
        if(Object.keys(obj).length>0){
            return true;
        }else{
            return false;
        }
    }else{
        console.log('参数类型预期错误');
    }
}

/*
*@method is_ObjKeyEmpty
*@param {String} str key值
*@param {Object} obj 类型为对象的数据
*@returns {Boolean} trur or false
*@desc 判断对象中该key值是否存在
*/
function is_ObjKeyEmpty(str='',obj={}){
    if(Inquire(obj)==='object'){
        for(let item in obj){
            if(item===str){
                return true;
            }
        }
    }else{
        console.log('参数类型预期错误');
    }
} 

/*
*@method is_arrEmpty
*@param {Object} arr 类型为数组的数据
*@returns {Boolean} trur or false
*@desc 判断数组是否为空
*/  
function is_arrEmpty(arr=[]){
    if(Inquire(arr)==='array'){
        if(arr.length>0){
            return true;
        }else{
            return false;
        }  
    }else{
        console.log('参数类型预期错误');
    }
}

/*
*@method getKey
*@param {String} val value值
*@param {Object} obj 类型为对象的数据
*@returns {Boolean} key值
*@desc 查找对象中value值对应的key值
*/
function getKey(val='',obj={}){
    if(Inquire(obj)==='object'){
        for(let item in obj){
            if(obj[item]===val){
                return item;
            }else{
                return 'Key not found!';
            }
        }
    }else{
        console.log('参数类型预期错误');
    }
}
