//首页右侧列表页
import React, { Component } from 'react';
import timg from '../image/timg.jpeg'
import { Link } from "react-router-dom";


function Li(props){
    function click(index){
        if(props.my.state.index===index){
            return false;
        }
        props.my.setState({
            index:index
        })
    }

    let li = props.list.map((item,index)=>
       <li className="art_list" key={item.title.toString()} 
           onClick={click.bind(this,index)}
           >
            <div className="list-text">
                <h3>
                    <Link to={{ pathname:'/article:'+item.id, state:{aid:item.id} }} 
                          style={{color:props.my.state.index===index&&'#fff'}}
                          >{item.title}</Link>
                </h3>
                <span>{item.brief}</span>
            </div>
            <div className="list-img">
                <img alt="zhengxiujing" src={timg} />
            </div>
       </li>
    )

    return li;
}

function Page(props){
    let ul = <ul className="page_ul">
        <Li data={props.data} my={props.my} page_num={props.page_num} />
    </ul>;

    function Li(props){
        let page_data = props.data;//所有数据
        let page_sum = props.data.length;//总数居长度
        let page_size = Math.ceil(page_sum/props.page_num);//分页页数  总长度/每页数量
        let page_init = [];
        let num = 0;
        let last_num = 0;

        for(var i=0;i<page_size;i++){
            page_init.push({
                num:i+1,
                data:[]
            });
            num = i*props.page_num;
            last_num = (i+1)*props.page_num;
            for(var v=num;v<last_num;v++){
                if(page_data[v]){
                    page_init[i].data.push(page_data[v]);                    
                }
            }
        }

        function click(my,current_data,index){     
            my.setState({
                current_data:current_data,
                active:index
            })
        }
        let li = page_init.map((item,index)=>
            <li id={index===props.my.state.active?'active':'none'} className="page_num" onClick={click.bind(this,props.my,item.data,index)} key={index+1}>{index+1}</li>
        )
        return li;
    }
    return ul;
}

class List extends Component {

    constructor(props){
        super(props);
        this.state = {
            datas:[],
            index:null,
            current_data:[],
            page_num:6,
            active:0
        }
    }

    componentDidMount(){
        let my =  this;
        global.id = false;
        global.public.server('article','GET','',function(data){
            let current = [];
            let temp = global.public.sort(data);
            for(var i=0;i<my.state.page_num;i++){
                current.push(temp[i]);
            }
            my.setState({
                datas:temp,
                current_data:current
            })
        });
    }

  render() {
    return (
        <ul className="drip_r_content">
            <Li list={this.state.current_data} my={this}/>
            <li className="page" style={{marginBottom:'30px'}}>
               <Page data={this.state.datas} page_num={this.state.page_num} my={this} />
            </li>    
        </ul>
    );
  }
}

export default List;