import React,{Component} from 'react'
import '../css/error.css'
import err_img from '../image/error.png'

function Errors(props){
    let err = <div className='error'>
        <img alt='' className='error_img' src={err_img} />
        <div className='error_msg'>
            <span className='msg_text'>系统信息提示：</span>
            {global.public.err_msg||'好像发生了什么错误！'}
            <span className='url_s'>{props.my.state.second+'秒后页面跳转...'}</span>
        </div>
    </div>;
    return err;
}

class Error extends Component {
    constructor(){
        super();
        this.state = {
            second:global.public.second>0?global.public.second:6,
            inter:null
        }
    }

    reciprocal(){
       let my = this; 
       let s = this.state.second;
       my.state.inter = setInterval(function(){
           if(s===0){
               clearInterval(my.state.inter);
               my.props.history.push(global.public.path||'/index');
           }
           if(s>0){
                my.setState({
                    second:s
                })
           }
           s = s-1;
       }, 1000);
    }

    componentDidMount(){
        this.reciprocal();
    }

    render(){
        return (
           <Errors my={this} />
        )
    }
}

export default Error;