import React, { Component } from 'react';
import '../public/public'
import '../public/config'
import '../css/content.css'

function Article(props){
    let time = typeof(props.data.time)==='string'&&props.data.time.split("T")[0];
    let art = <div className="content">
           <h2 className="title">{props.data.title}</h2>
           <div className="msg">
               <span className="left">来源：{props.data.source}</span>
            </div>
           <div className="text" dangerouslySetInnerHTML={{__html:props.data.content}}></div>
           <div className="msg">
               <span className="right">时间：{time}</span>
            </div>
    </div>;
    return art;
}

class Content extends Component {

    constructor(props){
        super(props);
        this.state = {
            datas:[]
        }
    }

    componentWillMount(){
        let my =  this;
        let aid = this.props.match.params.aid.split(':')[1];
        //如果传对象  可以用state  state传  state接
        //console.log(this.props.location.state.aid);
        if(aid){
            console.log("文章ID:"+aid);
            global.public.server('article','GET',aid,function(data){
                console.log(data);
                my.setState({
                    datas:data
                })
            });
        }else{
            return false;
        }
    }

    render(){
        return(
            <Article data={this.state.datas} />
        ) 
    }
}

export default Content;