import React,{Component} from 'react';
import ev from './event'
import '../public/public'
import '../css/admin.css'

function Td(props){
    function change(id){
        props.my.setState({
            del:id,
            alert:true,
        })
    }
    let list = props.data;
    let td = null;
    if(global.public.Inquire(list)==='array'){
        td = list.map((item,index)=>
            <tr key={index}>
                <td>{item.id}</td>
                <td>{item.title}</td>
                <td><span className='btn' onClick={change.bind(this,item.id)}>删除</span><span className='pad'>|</span><span className='btn'>编辑</span></td>
            </tr>  
        )
    }                          
    return td;
}

function Alert(props){
    function del(id){
        if(id!==0&&!props.my.state.click){
            props.my.setState({click:true});
            global.public.server('article','DELETE',id,function(data){
                if(global.public.is_ObjKeyEmpty('del',data)){
                    if(data.del){
                        global.public.server('article','GET','',function(data){
                            if(global.public.is_arrEmpty(data)){
                                props.my.setState({
                                    del:0,
                                    alert:false,
                                    data:data,
                                    click:false
                                });
                            }
                        });
                    }
                }
            });
        }
    }
    function hide(){
        props.my.setState({
            del:0,
            alert:false,
        })
    }
    let alt = <div className='alert' style={{display:props.my.state.alert?'block':'none'}}>
        <div className='msg'>确认删除吗?</div>
        <div className='btn'><span onClick={del.bind(this,props.my.state.del)}>删除</span><span onClick={hide}>取消</span></div>
    </div>
    return alt;
}

function Admins(props){
    function clear(){
        localStorage.setItem('drip_login',false);
        localStorage.setItem('drip_user','');
        localStorage.setItem('drip_code','');
        global.public.msg('/index','注销成功！',3,props.my);
        ev.emit('login',false);
    }

    function url_add(){
        props.my.props.history.push('/add');
    }

    let user_name = localStorage.getItem('drip_user');
    let admin = <div className='admin'>
                    <Alert my={props.my}/>
                    <span className='username'>管理员：{user_name}</span>
                    <button className='out' onClick={url_add}>添加文章</button>
                    <button className='out' onClick={clear}>注销</button>
                    <table cellSpacing="20">
                        <tbody>
                            <tr>
                                <th>ID</th>
                                <th>标题</th>
                                <th>操作</th>
                            </tr>
                            <Td data={props.data} my={props.my} />
                        </tbody>    
                    </table>
                </div>;
    return admin;
}

class Admin extends Component {
    constructor(props){
        super(props);
        this.state = {
            code:false,
            data:null,
            del:0,
            alert:false,
            click:false
        }
    }
    
    componentDidMount(){
        let my = this;
        global.public.login_code(this);
        global.public.server('article','GET','',function(data){
            if(global.public.is_arrEmpty(data)){
                my.setState({
                    code:false,
                    data:global.public.sort(data)
                });
            }
        });
    }

    render(){
        return (
          <Admins my={this} data={this.state.data} />
        )
    }
}

export default Admin;
