import React, { Component } from 'react';
import title from '../image/t.jpg';
import con from '../image/cop.png';
import out from '../image/logout.png';
import { HashRouter,Link } from 'react-router-dom';
import '../public/config'
import '../public/public'
import ev from './event'

let left = {
  navArr:global._config.index_nav,
  title:global._config.index_title,
  msg:global._config.index_msg,
  menuArr:global._config.index_nav
}

function Li(props){
  let li = props.data.map((item,index)=>
    <HashRouter key={index}><li><Link to={{ pathname:item.path}} replace={item.path === 'index'}>{item.name}</Link></li></HashRouter> 
  )
  return li;
}

function Menu(props){
 
  let dom = (
    <ul className='menuList'>
      <Li data={props.data} />
    </ul>
  )
  
  if(props.my.state.menu_stu){
    return dom;
  }else{
    return '';
  }

}

class Left extends Component {

  constructor(props){
    super(props);
    this.state = {
      menu_stu:false,
      glob_stu:false
    }
    global.public.glob_stu = this;
  }

  componentDidMount(){
    let stu = JSON.parse(localStorage.getItem('drip_login'));
    if(stu){
      this.setState({
        menu_stu:false,
        glob_stu:true
      })
    }else{
      this.setState({
        menu_stu:false,
        glob_stu:false
      })
    }
    this.eventEmitter = ev.addListener("login",(stu)=>{
        this.setState({
          menu_stu:false,
          glob_stu:stu
        })
    });
  }

  componentWillUnmount(){
    ev.removeListener(this.eventEmitter);
  }

  render() {

    let my = this;

    let click = function(e){
      e.stopPropagation();
      if(my.state.menu_stu){
        my.setState({
          menu_stu:false
        })
      }else{
        my.setState({
          menu_stu:true
        })
      }
    }

    return (
      <div className='drip_left'>
        <ul className="drip_l_nav">
            <li id="portrait">
              <div style={{display:my.state.glob_stu?'block':'none'}}>
                <HashRouter>
                  <Link to={{ pathname:'admin'}}><img alt='' src={title} /></Link>
                </HashRouter>
              </div>
              <div style={{display:!my.state.glob_stu?'block':'none'}}>
              <HashRouter>
                  <Link to={{ pathname:'login'}}><img alt='' src={out} /></Link>
                </HashRouter>
              </div>
            </li>
            
            <Li data={left.navArr} my={this} />

            <li id="menu" onClick={click}>
                <Menu data={left.menuArr} my={this} />
            </li>
        </ul>
        <div className="drip_l_content">
            <h2>{left.title}<img src={con} alt="" /></h2>
            <span>{left.msg}</span>
        </div>
      </div>
    );
  }
}

export default Left;
