import React,{ Component } from 'react';
import '../css/login.css'
import '../public/config'
import ev from './event'

function Logins(props){
    function log(props){
        let user = props.my.state.login.user;
        let pwd = props.my.state.login.pwd;
        let msg = '';
        const empty = /^\S*$/;
        const regEn = /[`~!@#$%^&*()_\-+=<>?:"{}|,.'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘’，。、]/im;
        if(!empty.test(user)||!empty.test(pwd)){
           msg = '输入不能为空';
           set();
           return false;
        }
        if(user.length<6||pwd.length<6){
            msg = '输入长度不够';
            set();
            return false;
        }
        if(regEn.test(user)||regEn.test(pwd)){
            msg = '输入不能包含特殊字符';
            set();
            return false; 
        }

        msg = '';
        set();

        let data = {
            user:user,
            password:pwd
        }    
        
        let history = props.my.props.history;

        global.public.server('login','POST',JSON.stringify(data),function(data){
            if(global.public.is_ObjKeyEmpty('code',data)){
                if(data.code){
                    localStorage.setItem('drip_login', true);
                    localStorage.setItem('drip_user', user);
                    localStorage.setItem('drip_code',pwd);
                    history.push('/admin');
                    ev.emit('login',true);
                }else{
                    msg = '用户名或密码错误！';
                    set();
                }
            }
        });

        function set(){
            props.my.setState({
                login:{
                    msg:msg,
                    user:user,
                    pwd:pwd
                }
            })
        }
    }

    function user(e){
        props.my.setState({
            login:{
                user: e.target.value,
                pwd:props.my.state.login.pwd
            }
        })
    }

    function pwd(e){
        props.my.setState({
            login:{
                user:props.my.state.login.user,
                pwd: e.target.value
            }
        })
    }

    let msg = props.my.state.login.msg;
    let from = <form className='from'>
        <input type='text' onChange={user.bind(this)} />
        <input type='password' onChange={pwd.bind(this)} />
        <span className="msg">{msg}</span>
        <button onClick={log.bind(this,props)}>登录</button>
    </form>

    return from;
}

class Login extends Component {
    constructor(props){
        super(props);
        this.state = {
            login:{
                msg:'',
                user:'',
                pwd:''
            }
        }
    }

    componentDidMount(){
        let code = localStorage.getItem('drip_login');
        if(!code){
            localStorage.setItem('drip_login',false);
        }
    }

    render(){
        return (
            <Logins my={this} />
        )
    }
}

export default Login;